<template>
  <div v-show="!moment">
    <label class="b-24-black" @click="updateShow" v-html="t('homeplay.ira')"></label>
    <br><br>
    <div class="audio-player">
      <canvas ref="canvasRef" width="256" height="200" class="visualizer"></canvas>

      <div class="song-info">
        <div class="d-flex justify-content-center pb-3">
          <div>
            <img v-show="!isFavorite" src="../assets/heart-gray.png" class="m-auto" style="width: 55px;"
              @click="AddFavorite()" data-bs-toggle="modal" data-bs-target="#modalFavorite">
            <img v-show="isFavorite" src="../assets/heart.png" class="m-auto" style="width: 55px;"
              @click="RemoveFavoriteModal()" data-bs-toggle="modal" data-bs-target="#modalFavorite">
          </div>
        </div>
        <div class="time">
          {{ formatTime(currentTime) }} / {{ formatTime(duration) }}

        </div>
        <input type="range" min="0" :max="duration" v-model="currentTime" @input="seekAudio" />
      </div>
      <div class="d-flex py-5">
        <img src="../assets/Atras.png" class="m-auto" @click="prevTrack">
        <img :src="isPlaying ? pauseIcon : playIcon" style="height: 100%; width: fit-content; margin: auto;"
          @click="togglePlay">
        <img src="../assets/Siguiente.png" class="m-auto" @click="nextTrack">
      </div>
    </div>

    <p class="r-14-gray pb-5">
      Music by <a href="https://open.spotify.com/artist/1XhgLxpSWwGVyxMUNUifV2?si=9F7rsZ7dQcG-Kz1cllta6g"
        target="_blank" rel="noopener noreferrer">CalmWaves</a>
      <br>
      Arr. by Angerpulse
    </p>

  </div>
  <div v-show="moment">
    <div>
      <div>
        <label class="b-24-black" v-html="t('when.estabas')"></label>
        <ul class="ps-0">
          <li class="list-group-item text-start r-16-gray py-2">
            <div class="ps-5 form-check">
              <input id="plan-m" class="form-check-input" selected type="radio" name="plan" value="1" aria-label="...">
              <label for="plan-m" class="form-check-label" v-html="t('when.act1')"></label>
            </div>
          </li>
          <li class="list-group-item text-start r-16-gray py-2">
            <div class="ps-5 form-check">
              <input id="plan-s" class="form-check-input" type="radio" name="plan" value="2" aria-label="...">
              <label for="plan-s" class="form-check-label" v-html="t('when.act2')"></label>
            </div>
          </li>
          <li class="list-group-item text-start r-16-gray py-2">
            <div class="ps-5 form-check">
              <input id="plan-a" class="form-check-input" type="radio" name="plan" value="3" aria-label="...">
              <label for="plan-a" class="form-check-label" v-html="t('when.act3')"></label>
            </div>
          </li>
          <li class="list-group-item text-start r-16-gray py-2">
            <div class="ps-5 form-check">
              <input id="plan-4" class="form-check-input" type="radio" name="plan" value="4" aria-label="...">
              <label for="plan-4" class="form-check-label" v-html="t('when.act4')"></label>
            </div>
          </li>
          <li class="list-group-item text-start r-16-gray py-2">
            <div class="ps-5 form-check">
              <input id="plan-5" class="form-check-input" type="radio" name="plan" value="5" aria-label="...">
              <label for="plan-5" class="form-check-label" v-html="t('when.act5')"></label>
            </div>
          </li>

        </ul>
        <div class="d-flex justify-content-center pt-3 pb-5">
          <div class="px-3">
            <button type="button" class="btn-gray-white" @click="CloseMoment()" v-html="t('homeplay.omitir')">
            </button>
          </div>
          <div class="px-3">
            <button type="button" class="btn-black border-0" @click="SetMoment()" v-html="t('homeplay.guardar')">
            </button>
          </div>
        </div>
      </div>

    </div>
  </div>


  <!-- Modal -->
  <div class="modal fade" id="modalFavorite" tabindex="-1" aria-labelledby="exampleModalLabel" aria-hidden="true">
    <div class="modal-dialog modal-dialog-centered">
      <div class="modal-content">
        <div class="modal-body px-3">
          <div class="p-4 d-flex justify-content-center">
            <label v-show="!isFavoriteQuestion" class="form-label r-24-gray" v-html="t('homeplay.agregar')"></label>
            <label v-show="isFavoriteQuestion" class="form-label r-24-gray" v-html="t('homeplay.remover')"></label>
          </div>
          <div class="d-flex justify-content-between pb-3" v-if="isFavoriteQuestion">
            <div>
              <button type="button" class="btn-gray" data-bs-dismiss="modal" v-html="t('homeplay.cancelar')">
              </button>
            </div>
            <div>
              <button type="button" class="btn-black" @click="RemoveFavorite()" data-bs-dismiss="modal"
                v-html="t('homeplay.aceptar')">
              </button>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
  <br><br>

</template>

<script setup>
import { ref, onMounted, onBeforeUnmount } from 'vue'
import * as bootstrap from 'bootstrap'
import { useI18n } from 'vue-i18n'
import { API_BASE_URL } from '@/config/api'

const { t } = useI18n()
let modalElement
let closeTimeout


onMounted(() => {
  modalElement = document.getElementById('modalFavorite')

  // Cuando el modal se muestre, programa su cierre
  modalElement.addEventListener('shown.bs.modal', () => {
    clearTimeout(closeTimeout) // evita m煤ltiples timers
    if (!isFavoriteQuestion.value) {
      closeTimeout = setTimeout(() => {
        const modalInstance = bootstrap.Modal.getInstance(modalElement)
        if (modalInstance) {
          modalInstance.hide()
        }
      }, 1000) // 1 segundo
    }
  })
})

onBeforeUnmount(() => {
  if (modalElement) {
    modalElement.removeEventListener('shown.bs.modal', () => { })
  }
  clearTimeout(closeTimeout)
})
const audios = ref([]);
function limpiarNombre(nombre) {
  return nombre.replace(/\.[^.]+(?=\.\w+$)/, '') // quita hash intermedio
}

const moment = ref(false)
const playIcon = new URL('@/assets/Play.png', import.meta.url).href
const pauseIcon = new URL('@/assets/Pausa.png', import.meta.url).href

const currentIndex = ref(0)
const isPlaying = ref(false)
const isShuffle = ref(true)
const currentTime = ref(0)
const duration = ref(0)
const durationSong = ref(0)
const audio = new Audio()
const isFavorite = ref(false)
const isFavoriteQuestion = ref(false)
const countPause = ref(0)
const idPlay = ref(0)

const canvasRef = ref(null)
let audioContext, analyser, dataArray, source, animationId
let shuffledIndices = []

const hasShownMessage = ref(false)
let playbackInterval = null
const playbackSeconds = ref(0)

function CloseMoment() {
  moment.value = false
}

function AddFavorite() {
  isFavorite.value = !isFavorite.value
  isFavoriteQuestion.value = false
  audios.value[currentIndex.value].estatus = true
  SetFavorite()
}

async function SetFavorite() {
  try {
    const sesion = localStorage.getItem("token")
    if (!sesion) return
    const currentAudio = audios.value[currentIndex.value]

    const response = await fetch(`${API_BASE_URL}/api/audioteca/SetAudioteca`, {
      method: "POST",
      headers: { "Content-Type": "application/json" },
      body: JSON.stringify({ username: sesion, audio: currentAudio.id + ''  })
    })

    if (!response.ok) throw new Error("Error en la petici贸n")

  } catch (error) {
    console.error("Error al cargar audios:", error)
  }
}

function RemoveFavoriteModal() {
  isFavoriteQuestion.value = true
}

function RemoveFavorite() {
  isFavorite.value = false
  isFavoriteQuestion.value = true
  audios.value[currentIndex.value].estatus = false
  DeleteFavorite()
}

async function DeleteFavorite() {
  try {
    const sesion = localStorage.getItem("token")
    if (!sesion) return
    const currentAudio = audios.value[currentIndex.value]

    const response = await fetch(`${API_BASE_URL}/api/audioteca/DelAudioteca`, {
      method: "POST",
      headers: { "Content-Type": "application/json" },
      body: JSON.stringify({ username: sesion, audio: currentAudio.id + ''  })
    })

    if (!response.ok) throw new Error("Error en la petici贸n")

  } catch (error) {
    console.error("Error al cargar audios:", error)
  }
}


function setupVisualizer() {
  audioContext = new (window.AudioContext || window.webkitAudioContext)()
  analyser = audioContext.createAnalyser()
  analyser.fftSize = 64
  const bufferLength = analyser.frequencyBinCount
  dataArray = new Uint8Array(bufferLength)

  source = audioContext.createMediaElementSource(audio)
  source.connect(analyser)
  analyser.connect(audioContext.destination)

  drawVisualizer()

  if (audioContext.state === 'suspended') {
    const resume = () => {
      audioContext.resume()
      document.removeEventListener('touchstart', resume)
    }
    document.addEventListener('touchstart', resume, { once: true })
  }
}

function drawVisualizer() {
  const canvas = canvasRef.value
  const ctx = canvas.getContext('2d')
  const width = canvas.width
  const height = canvas.height
  const centerY = height / 2

  function draw() {
    animationId = requestAnimationFrame(draw)
    analyser.getByteFrequencyData(dataArray)

    ctx.clearRect(0, 0, width, height)

    const barWidth = width / dataArray.length
    let x = 0

    for (let i = 0; i < dataArray.length; i++) {

      const value = dataArray[i]
      const barHeight = value / 2
      // ctx.fillStyle = '#aaa'
      const gradient = ctx.createLinearGradient(0, centerY - barHeight, 0, centerY + barHeight)
      gradient.addColorStop(0, '#00f')
      gradient.addColorStop(1, '#0ff')
      ctx.fillStyle = gradient
      ctx.fillRect(x, centerY - barHeight, barWidth, barHeight)
      ctx.fillRect(x, centerY, barWidth, barHeight)
      x += barWidth + 1
    }
  }

  draw()
}

function shuffleArray(length) {
  const indices = [...Array(length).keys()]
  for (let i = indices.length - 1; i > 0; i--) {
    const j = Math.floor(Math.random() * (i + 1))
      ;[indices[i], indices[j]] = [indices[j], indices[i]]
  }
  return indices
}

function loadTrack(index) {
  // audio.src = audios.value[index]
  const currentAudio = audios.value[index]
  audio.src = currentAudio.url

  //  sincroniza el coraz贸n seg煤n el valor de Estatus
  isFavorite.value = !!currentAudio.estatus
  durationSong.value = 0
  audio.load()
  stopPlaybackTimer()
  audio.play().then(() => {
    isPlaying.value = true
    startPlaybackTimer()
  }).catch(err => {
    console.warn('iOS puede haber bloqueado la reproducci贸n:', err)
  })
}

function togglePlay() {
  if (audio.paused) {
    startPlaybackTimer()
    audio.play()
    isPlaying.value = true
  } else {
    SetPlay(1)
    stopPlaybackTimer()
    audio.pause()
    isPlaying.value = false
    countPause.value++;
    if (playbackSeconds.value >= 60 | (playbackSeconds.value >= 57 && countPause.value > 2)) {
      moment.value = true;
      hasShownMessage.value = true
      playbackSeconds.value = 0
      countPause.value = 0
    }
  }
}

function startPlaybackTimer() {
  if (playbackInterval) return // Evita m煤ltiples intervalos

  playbackInterval = setInterval(() => {
    playbackSeconds.value++
    durationSong.value++
  }, 1000)
}

function stopPlaybackTimer() {
  clearInterval(playbackInterval)
  playbackInterval = null
}

function nextTrack() {
  SetPlay(0)
  if (isShuffle.value) {
    if (shuffledIndices.length === 0) {
      shuffledIndices = shuffleArray(audios.value.length)
    }
    currentIndex.value = shuffledIndices.shift()
  } else {
    currentIndex.value = (currentIndex.value + 1) % audios.value.length
  }
  loadTrack(currentIndex.value)
}

function prevTrack() {
  SetPlay(0)
  if (isShuffle.value) {
    if (shuffledIndices.length === 0) {
      shuffledIndices = shuffleArray(audios.value.length)
    }
    currentIndex.value = shuffledIndices.shift()
  } else {
    currentIndex.value = (currentIndex.value - 1 + audios.value.length) % audios.value.length
  }
  loadTrack(currentIndex.value)
}

function seekAudio() {
  audio.currentTime = currentTime.value
}

audio.addEventListener('timeupdate', () => {
  currentTime.value = audio.currentTime
  duration.value = audio.duration || 0
})

audio.addEventListener('ended', async () => {
  await SetPlay(0)
  nextTrack()
})

function formatTime(time) {
  const minutes = Math.floor(time / 60)
  const seconds = Math.floor(time % 60).toString().padStart(2, '0')
  return `${minutes}:${seconds}`
}

async function GetAudios() {
  try {
    const sesion = localStorage.getItem("token")
    if (!sesion) return
    const response = await fetch(`${API_BASE_URL}/api/config/GetAudiosUser`, {
    // const response = await fetch("https://marcoracm-002-site1.mtempurl.com/api/config/GetAudiosUser", {
      method: "POST",
      headers: { "Content-Type": "application/json" },
      body: JSON.stringify({ username: sesion })
    })

    if (!response.ok) throw new Error("Error en la petici贸n")

    const data = await response.json()
    
    
    const dataAudios = Array.isArray(JSON.parse(data.data)) ? JSON.parse(data.data) : data.data
    if (!Array.isArray(dataAudios)) throw new Error("dataAudios no es un array")


    const arr = dataAudios.map(item => {
      const limpio = limpiarNombre(item.Nombre)
      return {
        id: item.Audio,
        url: `/audios/${limpio}`,
        estatus: item.Estatus ?? false, //  guarda el valor de la API
        nombre: item.Nombre
      }
    })

//     const arr = dataAudios.map(item => {
//   const limpio = limpiarNombre(item.Nombre)
//   return `/audios/${limpio}`  // ruta relativa al public folder
// })
    // const arr = dataAudios.map(item => {
    //   const limpio = limpiarNombre(item.Nombre)
    //   return new URL(`@/assets/audios/${limpio}`, import.meta.url).href
    // })

    audios.value = arr

     // Cargar primer track solo despu茅s de tener audios
    if (audios.value.length > 0) {
      loadTrack(0)
    }

  } catch (error) {
    console.error("Error al cargar audios:", error)
  }
}


onMounted(() => {
  GetAudios()
  setupVisualizer()
  // loadTrack(currentIndex.value)
})

onBeforeUnmount(() => {
  cancelAnimationFrame(animationId)
  audio.pause()
  audio.src = ''
  if (audioContext) audioContext.close()
})

async function SetMoment() {
  try {
    const sesion = localStorage.getItem("token")
    if (!sesion) return
    // const currentAudio = audios.value[currentIndex.value]

    const response = await fetch(`${API_BASE_URL}/api/config/SetMoment`, {
      method: "POST",
      headers: { "Content-Type": "application/json" },
      // body: JSON.stringify({idMomento: document.querySelector('input[name="plan"]:checked').value, idUser: sesion, idAudio: currentAudio.id + ''  })
      body: JSON.stringify({idMomento: document.querySelector('input[name="plan"]:checked').value, idUser: sesion, idAudio: idPlay.value  })
    })
    moment.value = false
    if (!response.ok) throw new Error("Error en la petici贸n")

  } catch (error) {
    console.error("Error al cargar audios:", error)
    moment.value = false
  }
}

async function SetPlay(x) {
  try {
    const sesion = localStorage.getItem("token")
    if (!sesion) return
    const currentAudio = audios.value[currentIndex.value]

    const response = await fetch(`${API_BASE_URL}/api/config/SetPlay`, {
      method: "POST",
      headers: { "Content-Type": "application/json" },
      body: JSON.stringify({idReproduccion: idPlay.value, tiempo: durationSong.value, idUser: sesion, idAudio: currentAudio.id + '', home: true })
    })
    const data = await response.json()
    if (!response.ok) throw new Error("Error en la petici贸n")
    else{
      if(x==0)
        idPlay.value = 0
      else
      idPlay.value = data.data
    }
  } catch (error) {
    console.error("Error al cargar audios:", error)
  }
}
</script>


<style scoped>
.audio-player {
  text-align: center;
  background: #fff;
  padding: 1rem;
  border-radius: 8px;
  color: #555;
}

.visualizer {
  display: block;
  margin: 1rem auto;
  background-color: white;
}

.song-info {
  margin: 1rem 0;
}

.song-info h3 {
  margin: 0;
}

.song-info .time {
  font-size: 0.9rem;
  margin-bottom: 0.5rem;
}

.song-info input[type=range] {
  width: 100%;
}

.controls button {
  background: none;
  border: none;
  margin: 0 1rem;
  cursor: pointer;
  color: white;
}

.controls img {
  width: 40px;
  height: 40px;
}

.controls .active {
  color: lime;
}
</style>
